import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IFormBuilderEvent, IFormWidgetComponent, IPlainObject } from '@helix/platform/shared/api';
import { combineLatest, Observable } from 'rxjs';
import { ViewDesignerFacade } from '../../+state/view-designer.facade';
import { map } from 'rxjs/operators';
import { IRevertCustomizationOptions } from '@helix/platform/shared/components';

@Component({
  selector: 'rx-view-revert-customization',
  templateUrl: './view-revert-customization.component.html'
})
export class RxViewRevertCustomizationComponent implements IFormWidgetComponent {
  @Input()
  options: IPlainObject;

  @Input()
  isDisabled: boolean;

  @Output()
  events: EventEmitter<IFormBuilderEvent> = new EventEmitter();

  controlOptions$: Observable<IRevertCustomizationOptions> = combineLatest([
    this.viewDesignerFacade.getViewPropertyValue('allowOverlay'),
    this.viewDesignerFacade.getViewPropertyValue('scope'),
    this.viewDesignerFacade.getViewPropertyValue('overlayGroupId'),
    this.viewDesignerFacade.getViewPropertyValue('overlayDescriptor')
  ]).pipe(
    map(([allowOverlay, scope, overlayGroupId, overlayDescriptor]) => ({
      allowOverlay,
      scope,
      overlayGroupId,
      overlayDescriptor
    }))
  );

  constructor(private viewDesignerFacade: ViewDesignerFacade) {}
}
